import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import { Bar, getElementAtEvent } from "react-chartjs-2";
import { useFeedbackSubjectTeacherList } from "../../../Hooks/ApiHooks/InstituteFeedback/institute-feedback-api";
import { Suspense, lazy, useEffect, useRef, useState } from "react";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useTranslation } from "react-i18next";
import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CreateButton from "../../../Ui/Button/CreateButton";
const OneStaffAnalytic = lazy(() =>
  import("./OneStaffAnalytic/OneStaffAnalytic")
);
const FeedbackSelectDepartment = lazy(() =>
  import("../FeedbackSelectDepartment")
);
ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

const StudentFeedbackChart = ({ instituteId, ifid }) => {
  const { t } = useTranslation();
  const ref = useRef();
  const [open, setOpen] = useState("");
  const [analytic, setAnalytic] = useState(false);

  const labels = [];
  const [state, setState] = useState({
    labels,
    datasets: [
      {
        label: "Feedback %",
        data: [],
        barThickness: 8,
        backgroundColor: [
          "rgba(255, 99, 132, 0.2)",
          "rgba(255, 159, 64, 0.2)",
          "rgba(255, 205, 86, 0.2)",
          "rgba(75, 192, 192, 0.2)",
          "rgba(54, 162, 235, 0.2)",
          "rgba(153, 102, 255, 0.2)",
          "rgba(201, 203, 207, 0.2)",
        ],
        borderColor: [
          "rgb(255, 99, 132)",
          "rgb(255, 159, 64)",
          "rgb(255, 205, 86)",
          "rgb(75, 192, 192)",
          "rgb(54, 162, 235)",
          "rgb(153, 102, 255)",
          "rgb(201, 203, 207)",
        ],
        borderWidth: 0.5,
      },
    ],
  });
  const { feedbackSubjectTeacherList } = useFeedbackSubjectTeacherList({
    data: {
      ifid: ifid,
      page: 1,
      limit: 1000,
      search: "",
    },
    skip: !ifid,
  });

  useEffect(() => {
    if (feedbackSubjectTeacherList?.list?.length > 0) {
      let labels = [];
      let obj = {
        label: "Feedback %",
        data: [],
        // backgroundColor: "rgba(255, 99, 132, 0.5)",
      };
      for (let st of feedbackSubjectTeacherList?.list) {
        // if (st?.staff_emp_code) {
        if (st?.staffROLLNO) {
          let per = getInPercentage(
            st?.student_feedback?.[0]?.subject_analytic
          );
          labels.push(st?.staffROLLNO);
          // labels.push(st?.staff_emp_code);
          obj.data?.push(per);
        }
      }
      setState({
        labels,
        datasets: [obj],
      });
    }
  }, [feedbackSubjectTeacherList?.list]);

  const onClick = (event) => {
    let currect = getElementAtEvent(ref?.current, event);

    // for (let st of feedbackSubjectTeacherList?.list) {
    for (let i = 1; i <= feedbackSubjectTeacherList?.list?.length; i++) {
      if (currect?.[0]?.index === i) {
        setOpen(feedbackSubjectTeacherList?.list?.[i]);
      }
      // if (st?.staffROLLNO) {
      //   let per = getInPercentage(
      //     st?.student_feedback?.[0]?.subject_analytic
      //   );
      //   labels.push(st?.staffROLLNO);
      //   obj.data?.push(per);
      // }
    }
    // let currect = getDatasetAtEvent(ref?.current, event);
    // if (currect?.length > 0) {
    //   navigate(
    //     `/q/${params.username}/institute/member/feedback?a=feedback-detail`,
    //     {
    //       state: getQuery.state,
    //     }
    //   );
    // }
  };
  const onToggle = () => {
    setOpen("");
  };
  const onAnalyticToggle = () => {
    setAnalytic((pre) => !pre);
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("feedback_analytics")}</h6>
            <CreateButton
              label="export"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onAnalyticToggle}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          <Bar ref={ref} data={state} options={options} onClick={onClick} />
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      <Suspense fallback={<QvipleLoader />}>
        {open && (
          <OneStaffAnalytic
            onClose={onToggle}
            ifid={ifid}
            sid={open?._id}
            staff={open}
          />
        )}
        {analytic && (
          <FeedbackSelectDepartment onClose={onAnalyticToggle} ifid={ifid} />
        )}
      </Suspense>
    </>
  );
};

export default StudentFeedbackChart;

const options = {
  responsive: true,
  scales: {
    x: {
      grid: {
        offset: true,
      },
    },
  },
  // plugins: {
  //   legend: {
  //     position: "top",
  //   },
  //   title: {
  //     display: true,
  //     text: "Chart.js Bar Chart",
  //   },
  // },
};

const getInPercentage = (list) => {
  if (list?.length > 0) {
    let sum = 0;
    for (let lt of list) {
      sum += lt?.feedback_percentage;
    }
    // for (let lt of list) {
    //   sum += lt?.feedback_percentage;
    // }
    sum = Math.floor(sum / list?.length);
    return sum;
  }
  return 0;
};
